import React from "react"
import { graphql } from "gatsby"
import Image from "gatsby-image"
import Layout from "../components/layout"
import SEO from "../components/seo"
import css from "../styles/portfolio.module.scss"

export default ({ data }) => {
  return (
    <Layout heading="PORTFOLIO">
      <SEO title="portfolio" />

      <ul className={css.wrapper}>
        <li>
          <Image fluid={data.spotify.childImageSharp.fluid} />
          <p>SPOTIFY </p>
          <div className={css.info}>
            <p>
              Search for music in Spotify. Get artists recommendations. Create
              and Save playlist.
            </p>
            <p>REACT REDUX</p>
            <a
              rel="noopener noreferrer"
              href="https://spotify-music-search.netlify.com"
              target="_blank"
            >
              visit page
            </a>
          </div>
        </li>

        <li>
          <Image fluid={data.ok_kononov.childImageSharp.fluid} />
          <p>OK_KONONOV WEBSITE</p>
          <div className={css.info}>
            <p>Olexiy Kononov's online Fitness Trainings.</p>
            <p> GATSBY APOLLO GRAPHQL EXPRESS MONGODB</p>
            <a
              rel="noopener noreferrer"
              href="https://ok-kononov.netlify.com"
              target="_blank"
            >
              visit page
            </a>
          </div>
        </li>
        <li>
          <Image fluid={data.weather.childImageSharp.fluid} />
          <p>WEATHER</p>
          <div className={css.info}>
            <p>App to get and search current weather updates</p>
            <p> REACT </p>
            <a
              rel="noopener noreferrer"
              href="https://ssaavvaa.github.io/weather"
              target="_blank"
            >
              visit page
            </a>
          </div>
        </li>
        <li>
          <Image fluid={data.job_search.childImageSharp.fluid} />
          <p>JOBS SEARCHER</p>
          <div className={css.info}>
            <p>
              {" "}
              Web to search for a job , rate employers , as well as employees
            </p>
            <p> GATSBY APOLLO GRAPHQL EXPRESS MONGODB</p>
            <a
              rel="noopener noreferrer"
              href="https://ssaavvaa.github.io/job-finder"
              target="_blank"
            >
              visit page
            </a>
          </div>
        </li>
        <li>
          <Image fluid={data.images_search.childImageSharp.fluid} />
          <p>IMAGES SEARCH </p>
          <div className={css.info}>
            <p>
              API images searcher React App. With Masonry plugin arrangement
            </p>
            <p>JAVASCRIPT MASONRY</p>
            <a
              rel="noopener noreferrer"
              href="https://ssaavvaa.github.io/images-search__INTERSECTION-OBSERVER__REACT/"
              target="_blank"
            >
              visit page
            </a>
          </div>
        </li>
        <li>
          <Image fluid={data.go_it.childImageSharp.fluid} />
          <p>Go-IT LANDING</p>
          <div className={css.info}>
            <p> GO-IT landing page</p>
            <p>HTML CSS</p>
            <a
              rel="noopener noreferrer"
              href="https://ssaavvaa.github.io/Go-IT__LandingPage-HTML_CSS/"
              target="_blank"
            >
              visit page
            </a>
          </div>
        </li>
        <li>
          <Image fluid={data.minesweeper.childImageSharp.fluid} />
          <p>MINESWEEPER GAME</p>
          <div className={css.info}>
            <p>Old, loved by everyone from 90's - Minesweeper Game</p>
            <p>REACT REDUX</p>
            <a
              rel="noopener noreferrer"
              href="https://ssaavvaa.github.io/Minesweeper__REACT/"
              target="_blank"
            >
              visit page
            </a>
          </div>
        </li>
        <li>
          <Image fluid={data.events.childImageSharp.fluid} />
          <p>GO EVENTS</p>
          <div className={css.info}>
            <p>Event Maker. Create and search for events , live chat.</p>
            <p> GATSBY APOLLO GRAPHQL EXPRESS MONGODB</p>
            <a
              rel="noopener noreferrer"
              href="https://ssaavvaa.github.io/go-event__GATSBY-EXPRESS-GRAPHQL-APOLLO-MONGODB/"
              target="_blank"
            >
              visit page
            </a>
          </div>
        </li>
        <li>
          <Image fluid={data.movies.childImageSharp.fluid} />
          <p>MOVIES SEARCH</p>
          <div className={css.info}>
            <p>
              Get all the fresh released movies. Find movie by keyword. Get
              Movie cast and reviews ...
            </p>
            <p>REACT APP</p>
            <a
              rel="noopener noreferrer"
              href="https://w-da-my-movies.netlify.com/"
              target="_blank"
            >
              visit page
            </a>
          </div>
        </li>
        <li>
          <Image fluid={data.mogo.childImageSharp.fluid} />
          <p>MOGO LANDING</p>
          <div className={css.info}>
            <p>Mo-Go landing page</p>
            <p>HTML CSS</p>
            <a
              rel="noopener noreferrer"
              href="https://ssaavvaa.github.io/MoGo__Landing-page__HTML-CSS/"
              target="_blank"
            >
              visit page
            </a>
          </div>
        </li>
      </ul>
    </Layout>
  )
}

export const pageQuery = graphql`
  query Projects {
    spotify: file(relativePath: { eq: "projects/spotify.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 2000, quality: 100) {
          ...GatsbyImageSharpFluid_tracedSVG
        }
      }
    }
    ok_kononov: file(relativePath: { eq: "projects/ok-kononov.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 2000, quality: 100) {
          ...GatsbyImageSharpFluid_tracedSVG
        }
      }
    }
    weather: file(relativePath: { eq: "projects/weather.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 2000, quality: 100) {
          ...GatsbyImageSharpFluid_tracedSVG
        }
      }
    }
    job_search: file(relativePath: { eq: "projects/job-search.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 2000, quality: 100) {
          ...GatsbyImageSharpFluid_tracedSVG
        }
      }
    }
    mogo: file(relativePath: { eq: "projects/mogo.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 2000, quality: 100) {
          ...GatsbyImageSharpFluid_tracedSVG
        }
      }
    }
    images_search: file(relativePath: { eq: "projects/images-search.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 2000, quality: 100) {
          ...GatsbyImageSharpFluid_tracedSVG
        }
      }
    }
    go_it: file(relativePath: { eq: "projects/go-it.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 2000, quality: 100) {
          ...GatsbyImageSharpFluid_tracedSVG
        }
      }
    }
    minesweeper: file(relativePath: { eq: "projects/minesweeper.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 2000, quality: 100) {
          ...GatsbyImageSharpFluid_tracedSVG
        }
      }
    }
    events: file(relativePath: { eq: "projects/events.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 2000, quality: 100) {
          ...GatsbyImageSharpFluid_tracedSVG
        }
      }
    }
    movies: file(relativePath: { eq: "projects/movies.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 2000, quality: 100) {
          ...GatsbyImageSharpFluid_tracedSVG
        }
      }
    }
  }
`
